.droppable-container {
  border: 2px dashed #ccc;
  border-radius: 10px;
  padding: 20px;
  background: #fff;
  transition: border-color 0.3s, box-shadow 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  z-index: 1;
}

.drag-over {
  border-color: #018308;
  box-shadow: 0 0 20px rgba(1, 131, 8, 0.3);
}

.drag-and-drop-text {
  font-size: 20px;
  color: #333;
  margin-top: 12px;
}

.search-icon {
  width: 80px;
  height: 80px;
}
